@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.project_card{
    width: 450px;
    height: 480px;
    background-color: rgb(20, 20, 20);
    border-radius: 12px;
    box-shadow: 0 .25rem .25rem rgba(0,0,0,0.2),
    0 0 1rem rgba(0,0,0,0.2);
    opacity: 0;
    position: relative;
    .image_container{
        padding: 1em;
        img{
            width:100%;
            border-radius: 8px;
        }
    }
    .text_container{
        margin-top: -1rem;
        opacity: 0;
        padding: .8em 1em;
        .title{
            font-size:28px;
            color: lightgray;
            margin: 0;
        }
        .description{
            color: lightgray;
            font-size: 18px;
            margin: .4em 0 0 0;
        }
        .link{
            color: lightgray;
        }
    }
    button{
        border: 1px solid lightgray;
        border-radius: 8px;
        padding: .4em .8em;
        color: lightgray;
        font-size:18px;
        background-color: transparent;
        cursor: pointer;
        transition: .2s;
        position: absolute;
        bottom: .8em;
        left: 1em;
        &:hover{
            border:1px solid transparent;
            background-color: lightgray;
            color: rgb(20, 20, 20);
        }
    }
    &.animation{
        animation: CardFadeIn .5s .2s forwards;
        .text_container{
            animation: ContentFadeIn .5s .5s forwards;
        }
    }
}

.poppins-regular {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-style: normal;
  }

  .poppins-bold {
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    font-style: normal;
  }
  
    @keyframes CardFadeIn {
        0% {transform: translate(-.5rem, -.5rem) scale(1.05); opacity: 0; filter: blur(1px);}
        50% {opacity: 1; filter: blur(1px);}
        100% {transform: translateY(0) scale(1.0); opacity: 1; filter: blur(0);}
    }

    @keyframes ContentFadeIn {
    0% { transform: translateY(-1rem); opacity: 0; }
    100% { transform: translateY(0); opacity: 1; }
    }

@media screen and (max-width:400px){
    .project_card{
        height: 385px;
        .text_container{
            .title{
                font-size: 18px;
            }
            .description{
                font-size: 14px;
            }
            .link{
                font-size: 14px;
            }
        }
    }
}